<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <el-row :gutter="20"
        ><el-col :span="8">
          <el-input placeholder="" clearable>
            <el-button
              slot="append"
              icon="el-icon-search"
            ></el-button> </el-input></el-col
      ></el-row>
    </el-card>

    <!--  -->
    <el-table :data="infoList" style="width: 100%" border stripe>
      <el-table-column prop="firecar_id" label="ID" width="180">
      </el-table-column
      ><el-table-column
        prop="firecar_belong_firehouse"
        label=""
        width="180"
      >
      </el-table-column>

      <el-table-column prop="firecar_num" label="" width="180">
      </el-table-column>
      <el-table-column prop="firecar_status" label="">
        <template slot-scope="scope">
          <el-tag type="success" v-if="scope.row.firecar_status === 0"
            ></el-tag
          >
          <el-tag type="warning" v-else-if="scope.row.firecar_status === 1"
            ></el-tag
          >
          <el-tag v-else></el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="apartment" label="" width="180">
      </el-table-column>
      <el-table-column label="" width="180">
        <template slot-scope="">
          <el-button
            type="primary"
            icon="el-icon-edit"
            size="mini"
            @click="editInfoDialogVisible = true"
          ></el-button>
          <el-button
            type="danger"
            icon="el-icon-delete"
            size="mini"
            @click="removeInfoById()"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editInfoDialogVisible"
      width="50%"
      @close="editDialogClosed"
    >
      <el-form ref="editFormRef" :model="editForm" label-width="80px">
        <el-form-item label=""> <el-input></el-input> </el-form-item
      ></el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editInfoDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editInfoDialogVisible = false"
          > </el-button
        >
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      infoList: [],
      editInfoDialogVisible: false,
      editForm: {},
    }
  },
  created() {
    this.getInfoList()
  },
  methods: {
    async getInfoList() {
      const { data: res } = await this.$axios.post('/firecar/allFirecar')
      if (res.status !== 0) {
        return this.$message.error('')
      }
      this.infoList = res.data
    },

    // 
    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },
    // Id
    async removeInfoById() {
      // 
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning',
        }
      ).catch((err) => err)

      //  confirm cancel
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      } else {
        this.$message.success('')
        this.getInfoList()
      }
    },
  },
}
</script>
<style lang="less" scoped>
.el-card {
  margin-top: 20px;
}
</style>