<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <el-row :gutter="20"
        ><el-col :span="8">
          <el-input placeholder="" clearable>
            <el-button
              slot="append"
              icon="el-icon-search"
            ></el-button> </el-input></el-col
      ></el-row>
    </el-card>
    <!--  -->
    <el-table :data="infoList" style="width: 100%" border stripe>
      <el-table-column type="index"> </el-table-column
      ><el-table-column prop="fireman_id" label="ID" width="100">
      </el-table-column>
      <el-table-column prop="fireman_username" label="" width="160">
      </el-table-column>
      <el-table-column
        prop="fireman_realname"
        label=""
        width="160"
      >
      </el-table-column
      ><el-table-column prop="fireman_password" label="" width="150">
      </el-table-column>
      <el-table-column prop="fireman_tel" label="" width="180">
      </el-table-column
      ><el-table-column
        prop="fireman_belong_firehouse"
        label=""
        width="160"
      >
      </el-table-column>
      <el-table-column label="" width="180">
        <template slot-scope="scope">
          <el-button
            type="primary"
            icon="el-icon-edit"
            size="mini"
            @click="showEditDialog(scope.row)"
          ></el-button>
          <el-button
            type="danger"
            icon="el-icon-delete"
            size="mini"
            @click="removeInfoByName(scope.row.fireman_username)"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="50%"
      @close="editDialogClosed"
    >
      <el-form
        ref="editFormRef"
        :model="editForm"
        :rules="editFormRules"
        label-width="150px"
      >
        <el-form-item label="" prop="fireman_id">
          <el-input
            v-model="editForm.fireman_id"
            disabled
          ></el-input> </el-form-item
        ><el-form-item label="" prop="fireman_username">
          <el-input v-model="editForm.fireman_username" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="fireman_realname">
          <el-input
            v-model="editForm.fireman_realname"
          ></el-input> </el-form-item
        ><el-form-item label="" prop="fireman_password">
          <el-input v-model="editForm.fireman_password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="fireman_tel">
          <el-input v-model="editForm.fireman_tel"></el-input> </el-form-item
        ><el-form-item label="" prop="fireman_belong_firehouse">
          <el-input
            v-model="editForm.fireman_belong_firehouse"
          ></el-input> </el-form-item
      ></el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editUserInfo"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    // 
    var checkMobile = (rule, value, cb) => {
      const regMobile = /^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/
      if (regMobile.test(value)) {
        return cb()
      }
      cb(new Error(''))
    }
    return {
      infoList: [],
      editDialogVisible: false,
      editForm: {},
      //
      editFormRules: {
        fireman_password: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 3,
            max: 10,
            message: '3~10',
            trigger: 'blur',
          },
        ],
        fireman_realname: [
          { required: true, message: '', trigger: 'blur' },
        ],
        fireman_tel: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkMobile, trigger: 'blur' },
        ],
        fireman_belong_firehouse: [
          {
            required: true,
            message: '',
            trigger: 'blur',
          },
        ],
      },
    }
  },
  created() {
    this.getInfoList()
  },
  methods: {
    //
    async getInfoList() {
      const { data: res } = await this.$axios.post('/fireman/allFirehouse')
      if (res.status !== 0) {
        return this.$message.error('')
      }
      this.infoList = res.data
    },
    //
    async showEditDialog(e) {
      this.editDialogVisible = true
      this.editForm = e
    },
    // 
    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },

    // 
    async editUserInfo() {
      this.$refs.editFormRef.validate(async (valid) => {
        if (!valid) return
        // 
        const e = this.editForm
        const query =
          '?belong_firehouse=' +
          e.fireman_belong_firehouse +
          '&password=' +
          e.fireman_password +
          '&tel=' +
          e.fireman_tel +
          '&username=' +
          e.fireman_username +
          '&realname=' +
          e.fireman_realname
        const { data: res } = await this.$axios.post(`/fireman/update` + query)

        if (res.status !== 0) {
          return this.$message.error('')
        }
        // 
        this.editDialogVisible = false
        // 
        this.getInfoList()
        // 
        this.$message.success('')
      })
    },

    // username
    async removeInfoByName(username) {
      // 
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning',
        }
      ).catch((err) => err)

      //  confirm cancel
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      } else {
        const { dat: res } = await this.$axios.post(
          '/fireman/delete' + '?username' + username
        )
        if (res.status !== 0) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.getInfoList()
      }
    },
  },
}
</script>
<style lang="less" scoped>
.el-card {
  margin-top: 20px;
}
</style>