<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <el-row :gutter="20"
        ><el-col :span="8">
          <el-input placeholder="" clearable>
            <el-button
              slot="append"
              icon="el-icon-search"
            ></el-button> </el-input></el-col
      ></el-row>
    </el-card>
    <!--  -->
    <el-table :data="callList" style="width: 100%" border stripe>
      <el-table-column type="index"> </el-table-column>
      <el-table-column prop="number" label="" width="180">
      </el-table-column>
      <el-table-column prop="location" label="" width="180">
      </el-table-column>
      <el-table-column prop="state" label="" width="120"
        ><template slot-scope="scope">
          <el-tag type="success" v-if="scope.row.state"></el-tag>
          <el-tag type="info" v-else></el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="time" label="">
        <template slot-scope="scope">{{
          scope.row.time | dateFormat
        }}</template>
      </el-table-column>
      <el-table-column prop="res_Name" label="" width="120">
      </el-table-column>
      <el-table-column prop="tel" label="" width="180">
      </el-table-column>
      <el-table-column label="" width="180">
        <template slot-scope="">
          <el-button
            type="primary"
            icon="el-icon-edit"
            size="mini"
            @click="editCallDialogVisible = true"
          ></el-button>
          <el-button
            type="danger"
            icon="el-icon-delete"
            size="mini"
            @click="removeCallById()"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editCallDialogVisible"
      width="50%"
      @close="editDialogClosed"
    >
      <el-form ref="editFormRef" :model="editForm" label-width="80px">
        <el-form-item label=""> <el-input></el-input> </el-form-item
      ></el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editCallDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editCallDialogVisible = false"
          > </el-button
        >
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      callList: [],
      editCallDialogVisible: false,
      editForm: {},
    }
  },
  created() {
    this.getCallList()
  },
  methods: {
    async getCallList() {
      const { data: res } = await this.$axios.get('/menu/callList/')
      this.callList = res
      console.log(this.callList)
    },

    // 
    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },
    // Id
    async removeCallById() {
      // 
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning',
        }
      ).catch((err) => err)

      //  confirm cancel
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      } else {
        this.$message.success('')
        this.getCallList()
      }
    },
  },
}
</script>
<style lang="less" scoped>
.el-card {
  margin-top: 20px;
}
</style>