<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <el-row :gutter="20"
        ><el-col :span="8">
          <el-input placeholder="" clearable>
            <el-button
              slot="append"
              icon="el-icon-search"
            ></el-button> </el-input
        ></el-col>
      </el-row>
    </el-card>
    <!--  -->
    <el-table :data="residentList" style="width: 100%" border stripe>
      <el-table-column type="index"> </el-table-column>
      <el-table-column prop="resident_id" label="ID" width="80">
      </el-table-column>
      <el-table-column prop="resident_username" label="" width="120">
      </el-table-column
      ><el-table-column prop="resident_password" label="" width="130">
      </el-table-column>
      <el-table-column
        prop="resident_realname"
        label=""
        width="180"
      >
      </el-table-column>
      <el-table-column prop="resident_tel" label="" width="180">
      </el-table-column>
      <el-table-column prop="resident_address" label="" width="180">
      </el-table-column>
      <el-table-column prop="resident_status" label="" width="120">
        <template slot-scope="scope">
          <el-tag type="success" v-if="scope.row.resident_status"></el-tag>
          <el-tag type="info" v-else></el-tag>
        </template>
      </el-table-column>
      <el-table-column label="" width="180">
        <template slot-scope="scope">
          <el-button
            type="primary"
            icon="el-icon-edit"
            size="mini"
            @click="showEditDialog(scope.row)"
          ></el-button>
          <el-button
            type="danger"
            icon="el-icon-delete"
            size="mini"
            @click="removeUserByName(scope.row.resident_username)"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="50%"
      @close="editDialogClosed"
    >
      <el-form
        :model="editForm"
        :rules="editFormRules"
        ref="editFormRef"
        label-width="80px"
      >
        <el-form-item label="" prop="resident_username">
          <el-input v-model="editForm.resident_username" disabled></el-input>
        </el-form-item>
        <el-form-item label="ID" prop="resident_id">
          <el-input v-model="editForm.resident_id"></el-input>
        </el-form-item>
        <el-form-item label="" prop="resident_password">
          <el-input
            v-model="editForm.resident_password"
          ></el-input> </el-form-item
        ><el-form-item label="" prop="resident_realname">
          <el-input v-model="editForm.resident_realname"></el-input>
        </el-form-item>
        <el-form-item label="" prop="resident_tel">
          <el-input v-model="editForm.resident_tel"></el-input>
        </el-form-item>
        <el-form-item label="" prop="resident_address">
          <el-input v-model="editForm.resident_address"></el-input>
        </el-form-item>
        <el-form-item label="" prop="resident_status">
          <el-input v-model="editForm.resident_status"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editUserInfo"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    // 
    var checkMobile = (rule, value, cb) => {
      // 
      const regMobile = /^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/
      if (regMobile.test(value)) {
        return cb()
      }
      cb(new Error(''))
    }
    return {
      residentList: [],
      editDialogVisible: false,
      editForm: {},
      //
      editFormRules: {
        resident_id: [
          { required: true, message: 'ID', trigger: 'blur' },
        ],
        resident_address: [
          { required: true, message: '', trigger: 'blur' },
        ],
        resident_password: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 3,
            max: 10,
            message: '3~10',
            trigger: 'blur',
          },
        ],
        resident_realname: [
          { required: true, message: '', trigger: 'blur' },
        ],
        resident_tel: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkMobile, trigger: 'blur' },
        ],
        resident_status: [
          { required: true, message: '', trigger: 'blur' },
        ],
      },
      query: {},
      onStatus: 0,
    }
  },
  created() {
    this.getResidentList()
  },
  methods: {
    //
    async getResidentList() {
      const { data: res } = await this.$axios.post(`/user/allUser`)
      if (res.status !== 0) {
        return this.$message.error('')
      } else {
        this.residentList = res.data
      }
      this.onStatus = 0
      this.$store.commit('resetOn')
      this.residentList.forEach((item) => {
        if (item.resident_status === 0) this.onStatus++
      })
      this.$store.commit('addOn', this.onStatus)
    },

    // 
    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },

    // username
    async removeUserByName(username) {
      // 
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning',
        }
      ).catch((err) => err)

      // confirm cancel
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      } else {
        const { data: res } = await this.$axios.post(
          '/user/delete' + '?username=' + username
        )
        if (res.status !== 0) {
          return this.$message.error('')
        } else {
          this.$message.success('')
          this.getResidentList()
        }
      }
    },
    //
    async showEditDialog(e) {
      this.editDialogVisible = true
      this.editForm = e
      console.log(e)
    },
    // 
    async editUserInfo() {
      this.$refs.editFormRef.validate(async (valid) => {
        if (!valid) return
        // 
        const { data: res } = await this.$axios.post(
          `/user/update`,
          this.editForm
        )

        if (res.status !== 0) {
          return this.$message.error('')
        }
        console.log(this.editForm)
        // 
        this.editDialogVisible = false
        // 
        this.getResidentList()
        // 
        this.$message.success('')
      })
    },
  },
}
</script>
<style lang="less" scoped>
.el-card {
  margin-top: 20px;
}
</style>